// 首页商品
import { request } from '@/api/reques.js';
import * as MemberApi from '@/api/member/member.js';
import { goWindow } from '@/api/navigate.js'; // 页面跳转
import config from '@/api/config.js';
export default {
	data() {
		return {
			data_list: [],
			data_lists: [],
			page_index: 1,
			page_count: 0,
			ischecked:false,  // 下架
			checkedArr:[],
			allchecked:false,
			single:false,   // 上架
			checkedAll:false,
			shelvesArr:[],
		};
	},
	props: {
		options: {
			type: Object,
			default: () => {
				return {};
			}
		},
		status: {
			type: Number,
			default: -1
		}
	},
	watch: {
		status:{
			handler(e){
				let _this = this;
				_this.page_idnex = 1;
				_this.status = e;
				_this.ischecked = false;
				_this.allchecked = false;
				_this.checkedArr = [];
				_this.single = false;   // 上架
				_this.checkedAll = false;
				_this.shelvesArr = [];
				_this.getData(_this.page_idnex)
			},
		},
	},
	filters:{
		filterImg(url){
			if(url){
				return config.domain + url
			}
		}
	},
	methods: {
		// 页面跳转
		handleDetail(goodsId,group,promotion_id = 0,state){
			if(state == 2){
				this.$c.msg("商品审核中")
			}else if(state == 3){
				this.$c.msg("审核未通过,请重新发布")
			}else{
				let groupId = parseInt(String(group).substr(0,1));
				this.$c.detailsJump(goodsId,groupId,promotion_id)
			}
			
		},
		

		getData(page_index) {
			let _this = this;
			MemberApi.getUserCarGoodsList({
				page_index: page_index,
				state: _this.status == -1 ? "" : _this.status,
				get_type: _this.status == -1 ? 3 : 0,
			})
			.then(res => {
				if(res.data == null){
					this.data_list = [];
					return
				}
				uni.showLoading({
					title: '加载中'
				});
				let data = res.data.data;
				/* for (let index in data) {
					data[index].pic_cover = config.domain + data[index].pic_cover;
					console.log(data)
				} */
				if (page_index > 1 && _this.status !== -1) {
					data = _this.data_list.concat(data);
				}else if(page_index > 1 && _this.status === -1){
					data = _this.data_lists.concat(data);
				}
				setTimeout(() => {
					if(_this.status === -1){
						_this.data_list = [];
						_this.data_lists = data;
					}else{
						_this.data_lists = [];
						_this.data_list = data;
					}
					console.log(data)
					_this.page_count = res.data.page_count;
					uni.hideLoading();
				}, 500);
					
				
			});
		},
		loadData(e) {
			let _this = this;
			if (_this.page_index < _this.page_count) {
				_this.page_index++;
			} else {
				return;
			}
			_this.getData(_this.page_index);
		},
		handleErr(e, index) {
			if (e.type == 'error') {
				this.data_list[index].pic_cover = '/static/images/list.jpg';
			}
		},
		// 接受议价
		handleAccept(id){
			MemberApi.agreeyj({
				offer_id:id
			})
			.then((res)=>{
				uni.showLoading({
					title: '加载中'
				});
				if(res.code > 0){
					uni.hideLoading();
					uni.showToast({
						title: '已接受',
						duration: 2000,
						success: () => {
							
						}
					});
				}
			})
		},
		// 拒绝议价
		handleRefused(id){
			MemberApi.cancelyj({
				offer_id:id
			})
			.then((res)=>{
				uni.showLoading({
					title: '加载中'
				});
				if(res.code > 0){
					uni.hideLoading();
					uni.showToast({
						title: '已拒绝',
						duration: 2000,
						success: () => {
							
						}
					});
				}
			})
			
		},
		// 查看详情
		checkDetail(id){
			goWindow(`/other_pages/rejectOptions/rejectOptions?id=${id}`);
		},
		// 选择车源下架
		checkboxChange(e){
			console.log(e.detail.value)
			this.checkedArr = e.detail.value;
			//this.allchecked = false;
			if(this.checkedArr.length > 0 && this.checkedArr.length == this.data_list.length){
				this.allchecked = true;
			}else{
				this.allchecked = false;
			}
		},
		// 全选下架
		shelvesChange(e){
			let value = e.detail.value
			if(value.length > 0){
				this.allchecked = true;
				this.data_list.forEach((item) => {
					//console.log(item)
					let itemId = String(item.goods_id);
					if(!this.checkedArr.includes(itemId)){
						this.checkedArr.push(itemId);
					}
				})
				this.ischecked = true;
			}else{
				this.ischecked = false;
				this.checkedArr = [];
			}
		},
		// 下架
		handleShelves(){
			let _this = this;
			uni.showModal({
			    title: '提示',
			    content: '重新上架需再次审核，确认下架吗？',
				confirmText: '下架',
				confirmColor: '#0657A6',
			    success: function (res) {
			        if (res.confirm) {
						_this.$c.showLoading('加载中')
			           MemberApi.goodsOffline({
			           	goods_ids:_this.checkedArr.join(","),
			           })
			           .then(res => {
							if(res.code > 0){
								setTimeout(() => {
									_this.$c.hideLoading();
									_this.getData(_this.page_idnex)
								},500)
								
							}
			           })
			        } else if (res.cancel) {
						_this.$c.hideLoading();
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		// 选择车源上架
		changeCheckbox(e){
			//console.log(e.detail.value)
			this.shelvesArr = e.detail.value;
			if(this.shelvesArr.length > 0 && this.shelvesArr.length == this.data_list.length){
				this.checkedAll = true;
			}else{
				this.checkedAll = false;
			}
		},
		// 全选上架
		changeShelves(e){
			let value = e.detail.value
			if(value.length > 0){
				this.checkedAll = true;
				this.data_list.forEach((item) => {
					//console.log(item)
					let itemId = String(item.goods_id);
					if(!this.shelvesArr.includes(itemId)){
						this.shelvesArr.push(itemId);
					}
				})
				this.single = true;
			}else{
				this.single = false;
				this.shelvesArr = [];
			}
		},
		
		// 上架
		handlePutaway(){
			let _this = this;
			console.log(_this.shelvesArr)
			MemberApi.goodsOnline({
				goods_ids:_this.checkedArr.join(","),
			})
			.then(res => {
				_this.$c.showLoading('加载中');
				if(res.code > 0){
					setTimeout(() => {
						_this.$c.hideLoading();
						_this.$c.msg("重新审核中，请耐心等待")
						_this.getData(_this.page_idnex)
					},500)
					
				}else{
					_this.$c.hideLoading();
					_this.$c.msg("上架失败")
				}
			})
			
			
		}
	}
}
